import { Challenge } from '../types/game';

class CodeExecutionError extends Error {
  constructor(message: string) {
    super(message);
    this.name = 'CodeExecutionError';
  }
}

interface Context {
  fn?: Function;
  [key: string]: any;
}

export const executeCode = async (
  code: string,
  input: any[] = [],
  expected: any,
  timeoutMs: number = 3000
): Promise<{ success: boolean; message: string }> => {
  return new Promise((resolve) => {
    const timeoutId = setTimeout(() => {
      resolve({
        success: false,
        message: '‚ùå ÂÆüË°åÊôÇÈñì„ÅåÈï∑„Åô„Åé„Åæ„ÅôÔºàÂà∂ÈôêÊôÇÈñì: 3ÁßíÔºâ',
      });
    }, timeoutMs);

    let errorMessage = '';
    try {
      const context: Context = {};
      const wrappedCode = `
        ${code}
        context.fn = ${code.trim()};
      `;
      
      eval(wrappedCode);
      
      if (typeof context.fn === 'function') {
        const result = context.fn(...input);

        // ÁµêÊûú„ÅÆÊ§úË®º
        let success = false;
        if (Array.isArray(expected)) {
          success = Array.isArray(result) && 
                   result.length === expected.length &&
                   result.every((val, idx) => val === expected[idx]);
        } else {
          success = result === expected;
        }

        resolve({
          success,
          message: success
            ? '‚ú® „ÉÜ„Çπ„Éà„Å´ÊàêÂäü„Åó„Åæ„Åó„ÅüÔºÅ'
            : `‚ùå ÊúüÂæÖÂÄ§: ${JSON.stringify(expected)}, ÂÆüÈöõ„ÅÆÂÄ§: ${JSON.stringify(result)}`,
        });
      } else {
        throw new Error('Function is not properly defined');
      }
    } catch (error) {
      // „Çø„Ç§„É†„Ç¢„Ç¶„Éà„ÅÆ„ÇØ„É™„Ç¢
      clearTimeout(timeoutId);

      if (error instanceof Error) {
        errorMessage = error.message;
      } else {
        errorMessage = 'An unknown error occurred';
      }

      resolve({
        success: false,
        message: errorMessage,
      });
    }
  });
}; 