interface LoginCredentials {
  email: string;
  password: string;
}

interface AuthResponse {
  token: string;
  user: {
    id: string;
    email: string;
    name?: string;
  };
}

interface RegisterCredentials {
  email: string;
  password: string;
}

interface StoredUser {
  password: string;
  user: AuthResponse['user'];
}

// „É≠„Éº„Ç´„É´„Çπ„Éà„É¨„Éº„Ç∏„Åã„Çâ„É¶„Éº„Ç∂„ÉºÊÉÖÂ†±„ÇíË™≠„ÅøËæº„ÇÄ
const loadUsersFromStorage = (): Map<string, StoredUser> => {
  const storedUsers = localStorage.getItem('users');
  if (storedUsers) {
    return new Map(JSON.parse(storedUsers));
  }
  return new Map();
};

// „É¶„Éº„Ç∂„ÉºÊÉÖÂ†±„Çí„É≠„Éº„Ç´„É´„Çπ„Éà„É¨„Éº„Ç∏„Å´‰øùÂ≠ò
const saveUsersToStorage = (users: Map<string, StoredUser>) => {
  localStorage.setItem('users', JSON.stringify(Array.from(users.entries())));
};

// „É¢„ÉÉ„ÇØ„É¶„Éº„Ç∂„Éº„Çπ„Éà„É¨„Éº„Ç∏„ÅÆÂàùÊúüÂåñ
const mockUserStorage = loadUsersFromStorage();

// „ÉÜ„Çπ„Éà„É¶„Éº„Ç∂„Éº„ÅÆËøΩÂä†ÔºàÂ≠òÂú®„Åó„Å™„ÅÑÂ†¥Âêà„ÅÆ„ÅøÔºâ
if (!mockUserStorage.has('test@example.com')) {
  mockUserStorage.set('test@example.com', {
    password: 'password123',
    user: {
      id: '1',
      email: 'test@example.com',
      name: 'Test User'
    }
  });
  saveUsersToStorage(mockUserStorage);
}

export const authApi = {
  login: async (credentials: LoginCredentials): Promise<AuthResponse> => {
    // ÊúÄÊñ∞„ÅÆ„É¶„Éº„Ç∂„ÉºÊÉÖÂ†±„ÇíË™≠„ÅøËæº„ÇÄ
    const users = loadUsersFromStorage();
    const userRecord = users.get(credentials.email);
    
    if (userRecord && userRecord.password === credentials.password) {
      return {
        token: 'mock-jwt-token',
        user: userRecord.user
      };
    }
    
    const error = new Error('„É≠„Ç∞„Ç§„É≥„Å´Â§±Êïó„Åó„Åæ„Åó„Åü„ÄÇ„É°„Éº„É´„Ç¢„Éâ„É¨„Çπ„Å®„Éë„Çπ„ÉØ„Éº„Éâ„ÇíÁ¢∫Ë™ç„Åó„Å¶„Åè„Å†„Åï„ÅÑ„ÄÇ');
    error.name = 'AuthError';
    throw error;
  },

  register: async (credentials: RegisterCredentials): Promise<AuthResponse> => {
    // ÊúÄÊñ∞„ÅÆ„É¶„Éº„Ç∂„ÉºÊÉÖÂ†±„ÇíË™≠„ÅøËæº„ÇÄ
    const users = loadUsersFromStorage();

    if (credentials.email && credentials.password.length >= 8) {
      // Êó¢Â≠ò„É¶„Éº„Ç∂„Éº„ÉÅ„Çß„ÉÉ„ÇØ
      if (users.has(credentials.email)) {
        const error = new Error('„Åì„ÅÆ„É°„Éº„É´„Ç¢„Éâ„É¨„Çπ„ÅØÊó¢„Å´ÁôªÈå≤„Åï„Çå„Å¶„ÅÑ„Åæ„Åô„ÄÇ');
        error.name = 'RegisterError';
        throw error;
      }

      const newUser = {
        id: Date.now().toString(),
        email: credentials.email,
        name: 'Ë¶ãÁøí„ÅÑÈ≠îÊ≥ï‰Ωø„ÅÑ'
      };

      // „É¶„Éº„Ç∂„ÉºÊÉÖÂ†±„Çí‰øùÂ≠ò
      users.set(credentials.email, {
        password: credentials.password,
        user: newUser
      });
      saveUsersToStorage(users);

      return {
        token: 'mock-jwt-token',
        user: newUser
      };
    }
    const error = new Error('ÁôªÈå≤„Å´Â§±Êïó„Åó„Åæ„Åó„Åü„ÄÇÂÖ•ÂäõÂÜÖÂÆπ„ÇíÁ¢∫Ë™ç„Åó„Å¶„Åè„Å†„Åï„ÅÑ„ÄÇ');
    error.name = 'RegisterError';
    throw error;
  },

  logout: async (): Promise<void> => {
    // „É¢„ÉÉ„ÇØ„ÅÆ„É≠„Ç∞„Ç¢„Ç¶„ÉàÂá¶ÁêÜ
    return Promise.resolve();
  }
}; 