import React, { createContext, useContext, useState, useEffect } from 'react';

interface LevelContextType {
  currentExp: number;
  currentLevel: number;
  expToNextLevel: number;
  totalExp: number;
  addExperience: (amount: number) => void;
  currentTitle: string;
  resetProgress: () => void;
}

const LevelContext = createContext<LevelContextType | undefined>(undefined);

export const calculateExpForNextLevel = (level: number): number => {
  const baseExp = 100;
  return Math.floor(baseExp * (level * 1.5));
};

export const getTitleForLevel = (level: number): string => {
  if (level >= 20) return "ÁÜüÁ∑¥È≠îÊ≥ï‰Ωø„ÅÑ";
  if (level >= 15) return "‰∏äÁ¥öÈ≠îÊ≥ï‰Ωø„ÅÑ";
  if (level >= 10) return "‰∏≠Á¥öÈ≠îÊ≥ï‰Ωø„ÅÑ";
  if (level >= 5) return "Ë¶ãÁøí„ÅÑÈ≠îÊ≥ï‰Ωø„ÅÑ";
  return "È≠îÊ≥ïË¶ãÁøíÁîü";
};

// LocalStorage„ÅÆ„Ç≠„Éº
const LEVEL_DATA_KEY = 'magicCodeLearning_levelData';

// LocalStorage„Åã„ÇâÁµåÈ®ìÂÄ§„Éá„Éº„Çø„ÇíË™≠„ÅøËæº„ÇÄ
const loadLevelData = () => {
  const savedData = localStorage.getItem(LEVEL_DATA_KEY);
  if (savedData) {
    return JSON.parse(savedData);
  }
  return null;
};

// LocalStorage„Å´ÁµåÈ®ìÂÄ§„Éá„Éº„Çø„Çí‰øùÂ≠ò
const saveLevelData = (data: {
  currentExp: number;
  currentLevel: number;
  totalExp: number;
}) => {
  localStorage.setItem(LEVEL_DATA_KEY, JSON.stringify(data));
};

// LocalStorage„ÅÆ„Éá„Éº„Çø„Çí„ÇØ„É™„Ç¢
const clearLevelData = () => {
  localStorage.removeItem(LEVEL_DATA_KEY);
};

export const LevelProvider: React.FC<{ children: React.ReactNode }> = ({ children }) => {
  // ÂàùÊúüÂÄ§„ÅÆË®≠ÂÆöÔºàLocalStorage„Åã„ÇâË™≠„ÅøËæº„ÇÄ„Åã„ÄÅ„Éá„Éï„Ç©„É´„ÉàÂÄ§„Çí‰ΩøÁî®Ôºâ
  const savedData = loadLevelData();
  const [currentExp, setCurrentExp] = useState(savedData?.currentExp ?? 0);
  const [currentLevel, setCurrentLevel] = useState(savedData?.currentLevel ?? 1);
  const [totalExp, setTotalExp] = useState(savedData?.totalExp ?? 0);
  const [expToNextLevel, setExpToNextLevel] = useState(calculateExpForNextLevel(savedData?.currentLevel ?? 1));
  const [currentTitle, setCurrentTitle] = useState(getTitleForLevel(savedData?.currentLevel ?? 1));

  const checkLevelUp = () => {
    if (currentExp >= expToNextLevel) {
      const newLevel = currentLevel + 1;
      setCurrentLevel(newLevel);
      setCurrentExp(currentExp - expToNextLevel);
      setExpToNextLevel(calculateExpForNextLevel(newLevel));
      setCurrentTitle(getTitleForLevel(newLevel));
    }
  };

  const addExperience = (amount: number) => {
    setCurrentExp((prev: number) => prev + amount);
    setTotalExp((prev: number) => prev + amount);
  };

  const resetProgress = () => {
    clearLevelData();
    setCurrentExp(0);
    setCurrentLevel(1);
    setTotalExp(0);
    setExpToNextLevel(calculateExpForNextLevel(1));
    setCurrentTitle(getTitleForLevel(1));
  };

  // ÁµåÈ®ìÂÄ§„Éá„Éº„Çø„ÅÆÂ§âÊõ¥„ÇíÁõ£Ë¶ñ„Åó„Å¶LocalStorage„Å´‰øùÂ≠ò
  useEffect(() => {
    saveLevelData({
      currentExp,
      currentLevel,
      totalExp,
    });
  }, [currentExp, currentLevel, totalExp]);

  useEffect(() => {
    checkLevelUp();
  }, [currentExp]);

  return (
    <LevelContext.Provider
      value={{
        currentExp,
        currentLevel,
        expToNextLevel,
        totalExp,
        addExperience,
        currentTitle,
        resetProgress,
      }}
    >
      {children}
    </LevelContext.Provider>
  );
};

export const useLevel = () => {
  const context = useContext(LevelContext);
  if (context === undefined) {
    throw new Error('useLevel„ÅØ LevelProvider ÂÜÖ„Åß‰ΩøÁî®„Åô„ÇãÂøÖË¶Å„Åå„ÅÇ„Çä„Åæ„Åô');
  }
  return context;
}; 